import axios from 'axios'

export function calcPuntaje (cantSop, cantRec, pUnd, pMax) {
  if (cantSop > cantRec) {
    if (pMax > pUnd) {
      return pMax
    } else {
      return pUnd
    }
  } else if (cantSop === cantRec) {
    return pUnd
  } else {
    return 0
  }
}

export function getCarreras () {
  return new Promise((resolve, reject) => {
    axios.get('https://www.datos.gov.co/resource/shx5-k4uu.json')
      .then(response => {
        resolve(response)
      })
      .catch(err => {
        reject(err)
      })
  })
}

export function getMonth (num) {
  const meses = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre']
  return meses[num]
}

export function b64toBlobUrl (b64Data, contentType, sliceSize) {
  contentType = contentType || ''
  sliceSize = sliceSize || 512
  var byteCharacters = atob(b64Data)
  var byteArrays = []
  for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
    var slice = byteCharacters.slice(offset, offset + sliceSize)
    var byteNumbers = new Array(slice.length)
    for (var i = 0; i < slice.length; i++) {
      byteNumbers[i] = slice.charCodeAt(i)
    }
    var byteArray = new Uint8Array(byteNumbers)
    byteArrays.push(byteArray)
  }
  var blob = new Blob(byteArrays, { type: contentType })
  const urlBlob = URL.createObjectURL(blob)
  return urlBlob
}
